.info {
    &__container {
        padding-top: 79px;
        padding-bottom: 83px;

        @media (max-width: 576px) {
            padding-top: 52px;
            padding-bottom: 53px;
        }
    }

    &__title {
        max-width: 800px;
        margin-bottom: 17px;

        @media (max-width: 576px) {
            font-size: 20px;
            line-height: 27px;
            margin-bottom: 32px;
            text-align: center;
        }
    }

    &__desc {
        margin: 0;
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 21px;

        &.active {
            max-height: 400px;
            &:after {
                opacity: 0;
                visibility: hidden;
            }
        }

        @media (max-width: 576px) {
            position: relative;
            font-size: 14px;
            line-height: 18px;
            text-align: center;
            max-height: 92px;
            overflow: hidden;
            margin-bottom: 10px;
            will-change: max-height;
            transition: 0.3s ease-out;
            &:after {
                @include pseudo();
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                z-index: 1;
                opacity: 1;
                visibility: visible;
                transition: opacity 0.3s, visibility 0.3s;
                background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #fff 91.15%);
            }
        }
    }

    &__link {
        @media (max-width: 576px) {
            display: none;
        }
    }

    &__more {
        display: none;
        margin: 0 auto;
        font-size: 13px;
        width: 100%;
        padding: 6px 0 6px;
        @media (max-width: 576px) {
            display: block;
        }
    }
}